import PropTypes from 'prop-types';

// @mui
import { Box, Stack, Typography, useTheme } from '@mui/material';

// third-party
import { keys, includes } from 'lodash';

// project
import PageContainer from 'layouts/PageContainer';
import PageHeading from 'layouts/PageHeading';

const TypoBlock = (props) => {
    const theme = useTheme();
    const { typoName, typoObject, sx } = props
    return (

        <Box sx={{
            display: 'grid',
            gridTemplateColumns: "repeat(7, 1fr)",
            gridAutoFlow: "row",
            alignItems: 'center',
            width: 500,
            height: 52,
            p: 1,
            bgcolor: theme.palette.mode === 'dark' ? 'grey.600' : 'grey.300',
            color: theme.palette.text.default,
            borderRadius: 1,
            ...typoObject,
            ...sx
        }}>
            <Typography variant='inherit' sx={{ gridColumn: "1/2", gridRow: "1 / 2" }}>ABC</Typography>
            <Typography variant='inherit' sx={{ gridColumn: "2/3", gridRow: "1 / 2" }}>{typoName}</Typography>
            <Typography variant='inherit' sx={{ gridColumn: "4/8", gridRow: "1 / 2" }}>
                {typoObject.fontSize} {' / '}
                {typoObject.fontWeight}
            </Typography>
        </Box>
    )
}

TypoBlock.propTypes = {
    typoName: PropTypes.string,
    typoObject: PropTypes.object,
    sx: PropTypes.oneOfType([
        PropTypes.arrayOf(
            PropTypes.oneOfType([PropTypes.func, PropTypes.object, PropTypes.bool]),
        ),
        PropTypes.func,
        PropTypes.object,
    ]),
};

const UITypography = () => {
    const theme = useTheme();
    const mainTypo = ['h1', 'h2', 'h3', 'h4', 'h5', 'h6']
    const otherTypo = ['subtitle1', 'subtitle2', 'body1', 'body2', 'caption', 'overline']
    return (
        <PageContainer>
            <PageHeading title="Typography" />
            {/* <p>
                `
                h1 h2 h3  Heading text
                h4        Page Headings
                h5        Section headings
                h6        Card Headings
                Body1     Components and section headings
                body2     Heavily used in most components
                Subtitle1 Components
                Subtitle2 Labels
                Caption   Helper text
                Overline  Subtitles
                `
            </p> */}
            <Stack direction={'row'} justifyContent={'space-around'} spacing={3} mt={5} >
                <Stack
                    spacing={2}

                >
                    {
                        keys(theme.typography).map((typo, index) => (
                            includes(mainTypo, typo) &&
                            <TypoBlock typoName={typo} typoObject={theme.typography[typo]} />

                        ))
                    }
                </Stack>
                <Stack spacing={2} mt={5}>
                    {
                        keys(theme.typography).map((typo, index) => (
                            includes(otherTypo, typo) &&
                            <TypoBlock typoName={typo} typoObject={theme.typography[typo]} />
                        ))
                    }
                </Stack>
            </Stack>
        </PageContainer>
    )
};

export default UITypography;
